<template>
    <div id="typein" >
        <form v-if="!submited">
            <label>标题</label>
            <input type="text" v-model="blog.title" />
            <label>内容</label>
            <textarea v-model="blog.content"></textarea>
            <div>
                <label>vue</label>
                <input type="checkbox" value="vue" v-model="blog.categories">
                <label>react</label>
                <input type="checkbox" value="react" v-model="blog.categories">
                <label>angular</label>
                <input type="checkbox" value="angular" v-model="blog.categories">
            </div>
            <label></label>
            <select v-model="blog.author">
                <option v-for="author in authors">{{author}}</option>
            </select>
            <button v-on:click.prevent="addBlog">submit</button>
        </form>
        <div v-if="submited">
            <h3>提交成功！</h3>
        </div>
        <hr>
        <div>
            标题：<h3>{{blog.title}}</h3>
            内容：<p>{{blog.content}}</p>
            类别：<ul>
                <li v-for="category in blog.categories">{{category}}</li>
            </ul>
            作者：<p>{{blog.author}}</p>
        </div>
        
    </div>
</template>
<script>
export default {
    name: 'add-blog',
    data(){
        return {
            blog: {
                title:'',
                content:'',
                categories:[],
                author:'',
            },
            authors: ['li', 'lan', 'hang'],
            submited: false
        }
    },
    methods:{
        addBlog:function(){
            
            this.$http.post('https://vue-learn-1992.firebaseio.com/posts.json', this.blog)
                .then(function(data){
                    this.submited = true;
                    console.log(data);
                })
        }
    }
}
</script>
<style scoped>
    #typein{
        display: block;
        text-align: center;
    }
</style>



