<template>
    <div v-theme:column="'wide'" id="showblog">
        搜索：<input type="text" v-model="search">
        <div v-for="blog in searchTitle" class="sigle-blog">
            <router-link v-bind:to="'/blog/' + blog.id">
                <h1 v-rainbow>{{blog.title | toUppercase}}</h1>
            </router-link>
            <p>{{blog.content | snippet}}</p>
            作者：{{blog.author}}
            分类<ul><li v-for="category in blog.categories">{{category}}</li></ul>
        </div>
    </div>
</template>
<script>
export default {
    name: "show-blogs",
    data(){
        return {
            blogs:[],
            search: '',
        }
    },
    computed:{
        searchTitle:function(){
            return this.blogs.filter((blog) => {
                return blog.title.match(this.search);
            })
        }
    },
    created(){
        this.$http.get('https://vue-learn-1992.firebaseio.com/posts.json')
            .then(function(data){
                // console.log(data.json());
                // this.blogs.push({title: data.});
                return data.json();
            })
            .then(function(data){
                // console.log(data);
                var blogsArray = [];
                for(let key in data){
                    // console.log(data[key]);
                    data[key].id = key;
                    blogsArray.push(data[key]);
                }
                this.blogs = blogsArray;
            })
    },
    filters: {
        toUppercase(value){
            return value.toUpperCase();
        },
        snippet(value){
            return value.slice(0,100) + '...';
        }
    },
    directives: {
        'theme':{
            bind(el, binding, vnode){
                if(binding.value == 'wide'){
                    el.style.width = "600px";
                }
                if(binding.arg == "column"){
                    el.style.background == '#fff';
                }
            }
        },
        'rainbow':{
            bind(el, binding, vnode){
                el.style.color = '#' + Math.random().toString(16).slice(2,8);
            }
        }
    }
}
</script>
<style scoped>
    #showblog{
        padding: 10px;  
        max-width: 1000px;
        margin: 0 auto;
    }
    .sigle-blog{
        padding:8px;
        margin:10px;
    }
    input[type='text']{
        padding: 10px;
        width:100%;
        box-sizing: border-box;
    }
</style>




