<template>
    <footer><h3>{{copyright}}</h3></footer>
</template>
<script>
export default {
    name: 'app-footer',
    data(){
        return {
            copyright: 'Copyright 2017 Vue',
        }
    }
}
</script>
<style>
footer{
    text-align: center;
    background-color: rgb(75, 240, 34);
}
h3{
    padding:10px;
    color:#000;
}
</style>




