<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <article>{{blog.author}}</article>
        分类:<ul><li v-for="category in blog.categories">{{category}}</li></ul>
    </div>
</template>
<script>
export default {
    name: 'single-blog',
    data(){
        return {
            id: this.$route.params.id,
            blog: {}
        }
    },
    created:function(){
        this.$http.get("https://vue-learn-1992.firebaseio.com/posts/" + this.id + ".json")
            .then(function(data){
                console.log(data);
                return data.json();
                // this.blog = data.body;
            })
            .then(function(data){
                this.blog = data;
            })
    }
}
</script>
<style scoped>
    #single-blog{
        max-width: 1000px;
        width: 800px;
        margin: 0 auto;
        padding: 10px;  
        background-color: #eee;
        border: 1px dotted #aaa;
    }
</style>




